// 将axios实例挂载到全局Vue上，以方便调用。

import axios from 'axios'
// 创建一个axios的实例
const axiosInstance = axios.create()

// 导出一个包含install函数的对象
export default {
    install(Vue) {
        Vue.prototype.$http = axiosInstance
    }
}

// 在主入口main.js中注册本文件导出的对象


