<template>
<div class="nav-movie-secondary">
    <ul>
        <li v-for="item in items" :key="item">
            <a href="#">{{ item }}</a>
        </li>
    </ul>
</div>
</template>

<script>
const items = ["影讯&购票", "选电影", "电视剧", "排行榜", "分类", "影评"];
export default {
  data() {
    return {
      items: items
    };
  }
};
</script>

<style lang="stylus" scoped>
.nav-movie-secondary
    width 1040px
    margin 0 auto
    clear both
    ul
        margin 0
        padding 0
    li 
        display inline-block
        padding 5px 15px 5px 0
        a
            font-size 14px
            color: #27a
            line-height 30px
            &:hover
                color: #fff
                background #27a
</style>








