<template>
<div class="nav-search">
    <el-input 
    placeholder="搜索电影、电视剧、综艺、影人" 
    v-model="input1" 
    class="input-with-select">
        <el-button slot="append" icon="el-icon-search" class="search-btn"></el-button>
    </el-input>
</div>
</template>

<script>
export default {
  data() {
    return {
      input1: "",
      select: ''
    };
  }
};
</script>

<style lang="stylus" scoped>
    .nav-search
        width: 500px;
        margin: 6px 0 0 25px;
        float left

    .search-btn
        background #919aa2
</style>





