<template>
<nav class="top-nav">
    <el-menu 
        :default-active="activeIndex2" 
        class="el-menu" mode="horizontal" 
        @select="handleSelect" 
        background-color="#545c64" 
        text-color="#fff" 
        active-text-color="#ffd04b">
        
        <el-menu-item
        v-for="(item, idx) of items"
        :item="item"
        :idx="idx"
        :key="idx"
        :index="(idx + 1).toString()"
        class="cust-el-menu-item">
            <a href="javascript:;" target="_blank">{{ item }}</a>
        </el-menu-item>

        <!-- <el-submenu index="11"
        class="cust-el-menu-item">
            <template slot="title">
              更多
            </template>
            <el-menu-item index="2-1" class="cust-el-sub-menu-item">选项1</el-menu-item>
            <el-menu-item index="2-2">选项2</el-menu-item>
            <el-menu-item index="2-3">选项3</el-menu-item>
        </el-submenu> -->
    </el-menu>
</nav>
</template>

<script>
const items = ["豆瓣", "读书", "电影", "音乐", "同城", "小组", "阅读", "FM", "时间", "市集"];
export default {
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      items: items,
      title: '123'
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  },
};
</script>

<style lang="stylus" scoped>
.top-nav
  .el-menu
    margin 0
    padding 0
    .cust-el-menu-item
      line-height 30px
      height 30px
.top-nav      
  .cust-el-menu-item-title
    height 50px
</style>



