<template>
<div class="screening-bd">
  <el-carousel indicator-position="outside">
    <!-- <el-carousel-item v-for="items in groups" :key="items"> -->
    <!-- <el-carousel-item v-for="(item, idx) of items1" :key="idx"> -->
    <el-carousel-item 
    v-for="(items, idx) of groups"
    :key="idx">
      <ul>
        <li v-for="(item, sidx) of items" :key="sidx">
          <ul>
            <li class="poster">
              <img :src="item.images.small" alt="picture">
              <!-- <div :style="getPicStyle(item.images.small)"></div> -->
            </li>
            <li class="title">{{ item.title }}</li>
            <li class="rating">⭐⭐⭐⭐⭐&nbsp;&nbsp;9.9</li>
            <li class="ticket_btn">
              <a href="#">选座购票</a>
            </li>
          </ul>
        </li>
      </ul>
    </el-carousel-item>
  </el-carousel>
</div>  
</template>

<script>
import $ from 'jquery'

export default {
  data() {
    // console.log('return data')
    return {
      groups:[]
    };
  },

  mounted: function () {
    this.$nextTick(function () {
      // this.getGruopsData(this.pushGruopsData)
      this.getGruopsDataByJsonP(this.pushGruopsData)
    })
  },

  methods: {
    getGruopsData: function (cb) {
      this.$http.get('/doubanapi/v2/movie/in_theaters')
        .then((res) => {
          // console.log('get res')
          cb(res.data, 5)
        })
    },

    getGruopsDataByJsonP: function (cb) {
      $.ajax({
        type: "get",
        async: false,
        url: "https://api.douban.com/v2/movie/in_theaters",
        dataType: "jsonp",
        jsonp: "callback", 
        jsonpCallback: "success_jsonpCallback", 
        success: function (res) {
          console.log('get res by JsonP')
          cb(res, 5)
        },
        error: function () {
          console.log('fail')
        }
      });
    },

    pushGruopsData: function (response, n) {
      console.log(response)
      const subjects = response.subjects
      for (let i = 0, j = 0, length = subjects.length; i < length; i += n) {
        this.groups.push(subjects.slice(i, i + n))
      }
      console.log(this.groups)
    },

    getPicStyle: function (url) {
      return `background : url( ${url} ) no-repeat center`
    }
  },

  computed: {

  }
};
</script>

<style lang="stylus" scoped>
.screening-bd
  ul
    margin 0
    padding 0
  li
    display inline-block
    background url('')
    ul
      margin 10px 0
      padding 0
      li
        display block
        padding 3px 30px 0 0
      .title
        width 100px
        height 16px
        font-size 14px
        text-align center
        line-height 18px
        text-overflow ellipsis
        overflow: hidden
        white-space: nowrap
      .rating
        text-align center
        color #e09015
        font-size 12px
      .poster>img
        width 115px
        height 161px
      .ticket_btn
        padding-left 15px
      .ticket_btn > a
        width 90px
        height 24px
        display inline-block
        background #268dcd
        text-align center
        font-size 14px
        line-height 24px
        color #fff

</style>





